<template>
   <div class="black-bg" v-if="모달 == true">
    <div class="white-bg">
      <h4>{{원룸들[state].title}}</h4>
      <p>{{원룸들[state].content}}</p>
      <input v-model="month">
      <p>{{ month }} 개월 선택함 : {{원룸들[state].price * month}} 원</p>
      <button @click="$emit('closeModal')">닫기</button>
    </div>
  </div>
</template>

<script>
export default {
    name: 'ModalBanner',
    data(){
        return{
            month : 1,
        }
    },
    watch : {
        month(a){
            if(a >= 13){
                alert("12까지만 입력해주세요")
                this.month = 1;
            }
            if (isNaN(a) == true){
             alert('숫자만 입력해 주세요');
             this.month = 1;
            }
        },
    },
    props : {
        원룸들 : Array,
        state : Number,
        모달 : Boolean,

    }
}
</script>

