<template>

  <Transition name="fade">
    <ModalBanner @closeModal="모달=false" :원룸들="원룸들" :state="state" :모달="모달"/>
  </Transition>

  <div class="menu">  
    <a v-for="a in menu" :key="a">{{a}}</a>
  </div>

 <DiscountBanner  v-if="showDiscount == true" />

<button @click="priceSort">가격순 정렬</button>
<button @click="sortBack">되돌리기</button>

 <TheCard @openmodal="모달=true; state= $event" :원룸="원룸들[i]" v-for="(a,i) in 원룸들" :key="a"/>


</template>

<script>

import data from './assets/oneroom.js';
import DiscountBanner from './DiscountBanner.vue';
import ModalBanner from './ModalBanner.vue';
import TheCard from './TheCard.vue';

export default {
  name: 'App',
  data(){
    return{
      state : 0,
      원룸들원본 : [...data],
      원룸들 : data,
      menu : ['home', 'shop', 'about'],
      모달 : false,
      showDiscount : true,
    }
  },
  methods: {

    priceSort(){
      this.원룸들.sort(function(a,b){
      return a.price - b.price
      })
    },

    sortBack(){
      this.원룸들 = [...this.원룸들원본];
    }
  },  

  
  mounted() {
      setTimeout(()=>{
        this.showDiscount = false;
      }, 2000);
    },
    
  components: {
    DiscountBanner : DiscountBanner,
    ModalBanner : ModalBanner,
    TheCard : TheCard,
  }
}
</script>

